import { ApiProperty } from '@nestjs/swagger';

import {
  IAttachedFileResponse,
  IProgressCheckReportResponse,
  IProjectBudgetChangeResponse,
  IProjectCloseReportResponse,
  IProjectCommonReportResponse,
  IProjectMeetingReportResponse,
  IProjectStatusUpdateReportResponse,
  IProjectWorkerReportResponse,
} from '../../application/interface';

export class AttachedFileResponse implements IAttachedFileResponse {
  @ApiProperty({
    title: 'File name',
  })
  file_name: string;

  @ApiProperty({ title: 'File path' })
  file_path: string;
}

export class ProjectCommonReportResponse
  implements IProjectCommonReportResponse
{
  @ApiProperty({
    title: 'Id',
  })
  id: string;
  @ApiProperty({
    title: 'Title',
    required: false,
  })
  title?: string;
  @ApiProperty({
    title: 'Text',
    required: false,
  })
  report_text?: string;

  @ApiProperty({
    title: 'Attached files',
    type: [AttachedFileResponse],
  })
  attached_files: AttachedFileResponse[];

  @ApiProperty({ title: 'Project id' })
  project_id: string;

  @ApiProperty({ title: 'Report date' })
  report_date: string;

  @ApiProperty({ title: 'Create user id' })
  create_user_id: string;
}

export class ProgressCheckReportResponse
  extends ProjectCommonReportResponse
  implements IProgressCheckReportResponse
{
  @ApiProperty({
    title: 'Execution budget unit id',
    description: 'UUIDv7',
  })
  execution_budget_unit_id: string;

  @ApiProperty({
    title: 'Progress report data',
    description: 'Json',
  })
  progress_report_data: object;

  @ApiProperty({ title: 'Total performance percentage' })
  total_performance_percentage: number;

  @ApiProperty({ title: 'Consumed workers percentage' })
  consumed_workers_percentage: number;

  @ApiProperty({ title: 'Reported worker ids' })
  reported_worker_ids: string[];
}

export class ProjectBudgetChangeResponse
  extends ProjectCommonReportResponse
  implements IProjectBudgetChangeResponse
{
  @ApiProperty({ title: 'Change reason code' })
  change_reason_code: string;
  @ApiProperty({ title: 'Billing client id' })
  billing_client_id: string;
}

export class ProjectCloseReportResponse
  extends ProjectCommonReportResponse
  implements IProjectCloseReportResponse
{
  @ApiProperty({ title: 'Close report checklist' })
  close_report_checklist: object;
}

export class ProjectMeetingReportResponse
  extends ProjectCommonReportResponse
  implements IProjectMeetingReportResponse {}

export class ProjectStatusUpdateReportResponse
  extends ProjectCommonReportResponse
  implements IProjectStatusUpdateReportResponse
{
  @ApiProperty({ title: 'Project status id' })
  project_status_code: string;
}

export class ProjectWorkerReportResponse
  extends ProjectCommonReportResponse
  implements IProjectWorkerReportResponse
{
  @ApiProperty({ title: 'Report type code', required: false })
  report_type_code?: string;

  @ApiProperty({ title: 'Reported worker ids' })
  reported_worker_ids: string[];
}
